export default {
	data() {
		return {
			mobile: '',
			platform: '',
			user: ''
		};
	},
	methods: {
		// 前往绑定手机号页面
		handleOpenBindMobile () {
			uni.navigateTo({
				url: '/pages/bind-mobile/bind-mobile'
			})
		},
		// 退出登录
		handleOutLogin () { 
			uni.showModal({
			    title: '退出登录',
			    content: '确定退出登录么',
			    success: function (res) {
			        if (res.confirm) {
						// 清除用户信息
						uni.removeStorageSync('userInfo')
						uni.removeStorageSync('token')
						uni.removeStorageSync('openId')
						uni.removeStorageSync('userId')
						uni.removeStorageSync('classTrue')
						uni.removeStorageSync('classTrueId')
			            uni.switchTab({
			            	url: '../index/index'
			            })
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		
			
		},
		// 前往富文本
		goToRichText (id) {
			if (id == 1) {
				this.titleName = '用户协议'
				uni.setStorageSync('richText', this.user)
			} else if (id == 2) {
				this.titleName = '关于平台'
				uni.setStorageSync('richText', this.platform)
			}
			uni.navigateTo({
				url: '../rich-text/rich-text?titleName=' + this.titleName
			})
		},
		clearStorage () {
			uni.showModal({
			    title: '清除缓存',
			    content: '确定清除缓存么',
			    success: function (res) {
			        if (res.confirm) {
			            uni.showToast({
			            	icon: 'success',
							title: '清除成功'
			            })
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		async getProtocol () {
			let res = await this.$fetch(this.$api.getProtocol)
			console.log(res)
			this.platform = res.data.platform
			this.user = res.data.user
		}
	},
	onLoad() {
		// this.mobile = uni.getStorageSync('user_info').phonenumber
		this.mobile = uni.getStorageSync('phone')
		this.getProtocol()
	}
}
